.banner {
  background-image: url(../img/banner.png);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 840px;
  color: $primary-text-color;
  font-weight: 600;

  @include phone {
    height: 394px;
  }

  .banner-content {
    text-align: right;

    p {
      &:first-child {
        @include font-size(36px);

        @include pad {
          @include font-size(20px);
        }

        @include phone {
          @include font-size(16px);
        }
      }

      &:last-child {
        @include font-size(64px);

        @include pad {
          @include font-size(48px);
        }

        @include phone {
          @include font-size(32px);
        }
      }
    }
  }
}

.feat {
  background: $secondary-bg-color;
  padding: 64px 0 80px;

  @include pad {
    padding: 40px 0 80px 0;
  }

  @include phone {
    padding: 32px 0 56px 0;
  }

  h2 {
    color: #650300;
    margin-bottom: 44px;

    @include pad {
      margin-bottom: 32px;
    }
  }

  &-list {
    @include pad {
      flex-wrap: wrap;
    }

    &-item {
      width: 25%;
      margin: 0 12px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-weight: 600;
      padding: 0 37px;

      @include pad {
        width: 48%;
        padding: 0 50px;
        margin-bottom: 20px;
      }

      @include phone {
        width: 100%;
        margin: 0 0 28px 0;
        padding: 0 64px;
      }

      &:nth-child(1) {
        margin-left: 0;
      }

      &:nth-child(2) {
        @include pad {
          margin-right: 0;
        }
      }

      &:nth-child(3) {
        @include pad {
          margin-left: 0;
        }
      }

      &:nth-child(4) {
        margin-right: 0;

        @include phone {
          margin: 0;
        }
      }

      img {
        margin-bottom: 40px;

        @include pad {
          margin-bottom: 16px;
        }
      }

      h3 {
        @include font-size(24px);
        margin-bottom: 4px;
        color: $primary-text-color;
        font-weight: 600;

        @include pad {
          margin-bottom: 8px;
        }
      }

      p {
        @include font-size(16px);
        text-align: justify;

        @include pad {}
      }
    }
  }
}

.classic {
  padding: 64px 0 80px;

  @include pad {
    padding: 40px 0;
  }

  @include phone {
    padding: 24px 0;
  }

  h2 {
    color: $primary-text-color;
    margin-bottom: 60px;

    @include pad {
      margin-bottom: 32px;
    }

    @include phone {
      margin-bottom: 16px;
    }
  }

  &-list-item {
    margin-bottom: 32px;

    @include pad {
      margin-bottom: 8px;
    }

    @include phone {
      position: relative;
    }

    p {
      @include font-size(48px);
      font-family: $font-family-title;
      font-style: italic;
      font-weight: 900;
      color: $primary-bg-color;

      @include pad {
        @include font-size(28px);
      }

      @include phone {
        color: $secondary-text-color;
      }
    }

    .photo {
      margin-right: 24px;

      @include pad {
        width: 62.5%;
      }

      @include phone {
        width: 100%;
        margin: 0;
      }
    }

    .type {
      @include pad {
        width: 37.5%;
        justify-content: center;
      }

      @include phone {
        width: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateY(-50%) translateX(-50%);
      }

      img {
        margin-right: 8px;

        @include phone {
          filter: brightness(1000);
        }
      }

      .icon {
        margin-right: 8px;
      }
    }

    &:nth-child(1) {
      .type img {
        @include pad {
          max-width: 32px;
        }
      }
    }

    &:nth-child(2) {
      flex-direction: row-reverse;

      @include pad {
        flex-direction: row;
      }

      .photo {
        margin-right: 0;
        margin-left: 24px;
        filter: grayscale(1);

        @include pad {
          margin-right: 24px;
          margin-left: 0;
        }

        @include phone {
          margin: 0;
        }

        &:hover {
          filter: grayscale(0);
        }
      }

      .type img {
        max-width: 48px;

        @include pad {
          max-width: 32px;
        }
      }
    }

    &:nth-child(3) {
      margin: 0;

      .type img {
        @include pad {
          max-width: 23px;
        }
      }
    }
  }
}

.design {
  background-image: url(../img/bg.png);
  background-position: center center;
  background-size: cover;
  padding: 64px 0 479px;

  @include pad {
    padding: 40px 0 417px;
  }

  @include phone {
    padding: 24px 0 189px;
  }

  a {
    text-decoration: none;
  }

  h2 {
    color: $secondary-text-color;
    margin-bottom: 60px;

    @include pad {
      margin-bottom: 32px;
    }

    @include phone {
      margin-bottom: 16px;
    }
  }

  &-photos {
    margin-bottom: 60px;

    @include pad {
      padding: 0 48px;
      margin-bottom: 40px;
    }

    @include phone {
      padding: 0 12px;
      margin-bottom: 20px;
    }

    .more-bg {
      position: relative;
      margin-right: 77px;
      max-width: 30%;

      @include pad {
        margin-right: 48px;
      }

      @include phone {
        width: 102px;
        margin-right: 45px;
      }

      .more {
        position: absolute;
        top: 50%;
        left: 20px;
        transform: translateY(-50%);
        width: 100%;

        @include pad {
          left: 12px;
        }

        a {
          background: #ffffff;
          color: $primary-bg-color;
          font-family: $font-family-title;
          font-weight: 700;
          @include font-size(72px);
          text-align: center;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 300px;

          @include pad {
            @include font-size(40px);
            width: 145px;
          }

          @include phone {
            @include font-size(26px);
            width: 90px;
          }
        }

        img {
          display: inline-block;
          margin-left: 17px;

          @include pad {
            width: 27px;
            height: 27px;
            margin-left: 10px;
          }

          @include phone {
            width: 17px;
            height: 17px;
            margin-left: 8px;
          }
        }
      }
    }
  }

  &-list {
    @include phone {
      flex-direction: column;
    }

    &-item {
      margin: 0 15px;

      @include phone {
        margin: 0 0 20px;
      }

      &:first-child,
      &:last-child {
        margin-left: 0;
        position: relative;

        &::after {
          content: "";
          position: absolute;
          background: rgba($color: #fff, $alpha: 0.5);
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          @include font-size(72px);
          font-family: $font-family-title;
          font-weight: 700;
          color: $primary-bg-color;
          display: flex;
          justify-content: center;
          align-items: center;

          @include pad {
            @include font-size(32px);
          }
        }
      }

      &:first-child::after {
        content: "Double A+";
      }

      &:last-child {
        margin-right: 0;
        margin-bottom: 0;

        &::after {
          content: "YOUTH";
        }
      }

      img {
        width: 100%;
      }
    }
  }
}

.rec {
  margin: -335px 0 80px;

  @include pad {
    margin: -377px 0 40px;
  }

  @include phone {
    margin: -165px 0 24px;
  }

  h2 {
    color: $secondary-text-color;
    margin-bottom: 60px;

    @include pad {
      margin-bottom: 32px;
    }

    @include phone {
      margin-bottom: 16px;
    }
  }

  &-list {
    @include pad {
      flex-wrap: wrap;
    }

    &-item {
      width: 25%;
      margin: 0 12px;

      @include pad {
        width: 48%;
        margin-bottom: 24px;
      }

      @media (max-width:414px) {
        width: 100%;
        margin: 0 0 16px;
      }

      img {
        height: 256px;
        display: block;
        width: 100%;
      }

      p {
        font-family: $font-family-title;
        font-size: 16px;
        line-height: 21px;
        font-weight: 400;
        margin-bottom: 16px;
      }

      &:nth-child(1) {
        margin-left: 0;
      }

      &:nth-child(2) {
        @include pad {
          margin-right: 0;
        }
      }

      &:nth-child(3) {
        @include pad {
          margin-left: 0;
          margin-bottom: 0;
        }

        @media (max-width:414px) {
          margin-bottom: 16px;
        }
      }

      &:nth-child(4) {
        margin-right: 0;
        margin-bottom: 0;
      }
    }
  }

  &-content {
    background: #fff;
    border: 1px solid #dee2e6;
    padding: 83px 16px 0;
    margin-top: -83px;

    p {
      &:nth-child(1) {
        font-weight: 500;
        margin-top: 16px;
      }

      &:nth-child(3) {
        color: #707070;
      }
    }
  }
}

.contact {
  padding: 64px 0 164px;
  background: $secondary-bg-color;

  @include pad {
    padding: 40px 0 116px;
  }

  @include phone {
    padding: 24px 0 104px;
  }

  h2 {
    margin-bottom: 24px;

    @include pad {
      margin-bottom: 16px;
    }

    @include phone {
      margin-bottom: 8px;
    }
  }

  p,
  label {
    @include font-size(20px);
    font-weight: 600;

    @include pad {
      @include font-size(16px);
    }
  }

  &-content {
    width: 66%;

    @include pad {
      width: 74%;
    }

    @include phone {
      width: 100%;
    }

    &>p {
      margin-bottom: 20px;

      @include pad {
        margin-bottom: 16px;
      }

      @include phone {
        margin-bottom: 12px;
      }
    }
  }

  .data {
    width: 66%;
    position: relative;

    @include pad {
      width: 74%;
    }

    @include phone {
      width: 100%;
    }

    label {
      @include font-size(16px);
      text-align: left;
      margin-bottom: 4px;

      @include phone {
        margin-bottom: 2px;
      }

      &:last-child {
        margin: 0;
      }
    }

    input {
      height: 50px;
      width: 100%;
      margin-bottom: 16px;
      border: 1px solid #000;
      background: transparent;

      @include pad {
        margin-bottom: 12px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    .feedback {
      width: 100%;
      margin-bottom: 16px;
      border: 1px solid #000;
      background: transparent;
    }

    .agreement {
      input {
        width: 16px;
        height: 16px;
        margin: 0 8px 0 0;
        background: transparent;

        @include phone {
          zoom: 1.5;
          margin-right: 2px;
        }
      }
    }

    .submit {
      @include font-size(16px);
      font-family: $font-family-title;
      font-weight: 900;
      width: 23%;
      height: 54px;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      background: $primary-bg-color;
      color: $secondary-text-color;
      margin-top: 42px;
      width: 180px;
      height: 54px;

      &:hover {
        background: #650300;
      }

      @include pad {
        margin-top: 24px;
        width: 136px;
        height: 36px;
      }

      @include phone {
        margin-top: 20px;
      }
    }
  }
}