.nav {
  background: $primary-bg-color;
  padding: 0 88px;

  a {
    text-decoration: none;
  }

  @include pad {
    padding: 0 6.25%;
  }

  @include phone {
    display: block !important;
    padding: 0;
    text-align: center;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 1;
  }

  .hamIcon {
    display: none;

    @include phone {
      display: block;
      position: absolute;
      top: 25px;
      left: 16px;
      transform: translateY(-50%);
    }
  }

  .logo {
    @include phone {
      padding: 10px 0;
    }

    a {
      display: block;
      background-image: url(../img/logo.png);
      background-repeat: no-repeat;
      background-size: contain;
      width: 80px;
      height: 49px;
      @include text-hide;

      @include phone {
        margin: auto;
        width: 48px;
        height: 29px;
      }
    }
  }

  .menu {
    li {
      margin-left: 64px;
      padding: 40px 0;

      @include phone {
        margin: 0;
        border-bottom: 1px solid #fff;
        padding: 16px 0;
      }
    }

    li:first-child {
      @include phone {
        border-top: 1px solid #fff;
      }
    }

    a {
      display: block;
      color: $secondary-text-color;
      @include font-size(20px);
    }

    @include phone {
      flex-direction: column;
      max-height: 0px;
      overflow: hidden;
      transition: all 0.5s;
      position: absolute;
      left: 0;
      right: 0;
      background: #aa0601;
    }


  }

  .active {
    max-height: 256px;
  }
}

.footer {
  background: $primary-bg-color;
  padding: 36px 0 27.43px;
  color: $secondary-text-color;
  @include font-size(16px);

  @include pad {
    padding: 40px 0;
  }

  @include phone {
    padding: 24px 0;
  }

  a {
    color: $secondary-text-color;
    text-decoration: none;

    @include pad {
      width: 24px;
    }
  }

  span {
    @include font-size(36px);
    font-family: "Roboto", sans-serif;
    font-weight: 400;

    @include pad {
      @include font-size(20px);
    }

    @include phone {
      @include font-size(16px);
    }
  }

  .container {
    padding: 0 25px;

    @include pad {
      padding: 0 48px;
    }

    @include phone {
      padding: 0 12px;
    }
  }

  &-contact {
    @include phone {
      display: flex;
      flex-direction: row-reverse;
      align-items: flex-start;
      justify-content: space-between;
      border-bottom: 1px solid #fff;
      margin-bottom: 16px;
    }
  }

  &-menu {
    @include phone {
      display: none !important;
    }

    li {
      margin-right: 48px;

      &:last-child {
        margin-right: 0;
      }

      @include pad {
        margin-right: 40px;
      }
    }
  }

  .socialMedia li {
    margin-left: 32px;

    &:first-child {
      margin-left: 0;
    }

    @include pad {
      margin-left: 24px;

      img {
        width: 24px;
      }
    }
  }

  .contactWay {
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid #fff;

    @include phone {
      margin-bottom: 0;
      border-bottom: none;
    }

    .tel img {
      margin-right: 23px;

      @include pad {
        margin-right: 12px;
        width: 23px;
        height: 23px;
      }

      @include phone {
        width: 13px;
        height: 13px;
        margin-right: 8px;
      }
    }

    .mail img {
      margin-right: 12px;

      @include pad {
        margin-right: 8px;
        width: 32px;
        height: 32px;
      }

      @include phone {
        width: 20px;
        height: 20px;
        margin-right: 5px;
      }
    }
  }

  &-logo {
    font-family: "Roboto", sans-serif;
    font-weight: 400;

    @include phone {
      flex-direction: column;
      align-items: flex-start !important;
      position: relative;
    }

    p {
      @include phone {
        margin-right: 0;
        margin-bottom: 8px;
      }
    }

    a {
      width: auto;

      @include phone {
        margin-bottom: 8px;
      }

      &:first-child {
        margin-right: 38px;

        @include pad {
          display: none;
        }

        @include phone {
          display: block;
          width: 46px;
          height: 28px;
          position: absolute;
          right: -36px;
          bottom: 0;
        }
      }

      &:last-child {
        margin-left: 62px;

        @include pad {
          margin-left: 24px;
        }

        @include phone {
          margin: 0;
        }
      }
    }
  }
}