@mixin pad {
  @media (max-width:992px) {
    @content;
  }
}

@mixin phone {
  @media (max-width:767px) {
    @content;
  }
}

@mixin text-hide {
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden;
}

@mixin font-size($fz) {
  font-size: $fz;
  line-height: $fz*1.5;
}