.faq {
  &-banner {
    background-image: url(../img/qa.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 304px;

    @include phone {
      height: 143px;
    }

    h2 {
      @include font-size(64px);
      margin-right: 10px;

      @include pad {
        @include font-size(48px);
        margin-right: 16px;
      }

      @include phone {
        @include font-size(28px);
      }
    }

    img {
      @include pad {
        max-width: 48px;
      }

      @include phone {
        max-width: 24px;
      }
    }
  }

  &-main {
    padding: 88px 0 156px;

    @include pad {
      padding: 40px 0 104px;
    }

    @include phone {
      padding: 32px 0 56px;
    }

    h3 {
      @include font-size(32px);
      font-weight: 700;
      margin-bottom: 16px;

      @include pad {
        @include font-size(28px);
        margin-bottom: 32px;
      }

      @include phone {
        @include font-size(20px);
        margin-bottom: 16px;
      }
    }
  }

  &-list-item a,
  &-list-item p {
    border: 1px solid #000;
    padding: 22px 40px;

    @include pad {
      padding: 16px 24px;
    }

    @include phone {
      padding: 16px 8px 16px 16px;
    }
  }

  &-list-item {
    a {
      text-decoration: none;
      color: #000000;
      display: block;
    }

    h4 {
      @include font-size(20px);
      font-weight: 700;

      @include pad {
        @include font-size(16px);
      }
    }

    p {
      @include font-size(16px);

      @include pad {
        @include font-size(14px);
      }
    }
  }

  .icon-down {
    display: none;
  }

  &-list-item.active {
    .icon-down {
      display: block;
    }

    .icon-up {
      display: none;
    }
  }
}